// For large devices
@media (max-width: 1136px){
	.container{
		width: auto;
		padding: 0px 50px;
	}

	.nav{
		margin-left: 35px;
	}

	header{
		.fp-controlArrow.fp-prev{
			left: 10px;
		}

		.fp-controlArrow.fp-next{
			right: 10px;
		}
	}
}

// For tablets
@media (max-width: 1024px){
	.nav{
		width: 100%;
		justify-content: flex-end;

		padding-left: 100px;
		padding-right: 100px;
	}
	#menu{
		display: none;
	}

	.hamburher{
		display: block;
	}

	header{
		.wrapper{
			grid-template-columns: 1fr;
		}

		.right-part{
			padding-left: 40px;
		}

		.blur{
			width: 100%;
			height: 100%;
			top: 0;

			transform: translateX(0) translateY(-100%);

			animation-name: blur-animation--tablet;
		}

		.logo{
			animation-name: logo-box-animation--tablet;
		}

		.offer{
			animation-name: offer-animation--tablet;
		}

		.principles{
			margin-bottom: 50px;
		}

		.fp-slides{
			position: static;
		}
	}
}

// For small tablets
@media (max-width: 581px){
	.container{
		padding-left: 25px;
		padding-right: 25px;
	}
	.bg-line{
		width: 15px;
		visibility: hidden;
	}
	#cursor{
		display: none;
	}
	.nav{
		padding-right: 30px;
		margin-left: 0;
		padding-left: 0;
	}

	.mobile-nav{
		width: 70%;
		padding-right: 30px;
		padding-top: 50px;

		a{
			font-size: 18px;
		}
	}

	header{
		.logo{
			height: auto;
			animation-name: logo-box-animation--mob;
		}

		.logo__top{
			width: 100px;
			transform: translateX(0) translateY(0);

			animation-name: logo-top-animation--mob, logo-top-size-animation--mob;
		}

		.logo__bottom{
			width: 170px;
		}

		.right-part{
			padding-left: 25px;
			padding-right: 25px;
		}

		.wrapper{
			display: flex;
		}

		.offer{
			margin-left: auto;
			margin-right: auto;
			animation-name: offer-animation--mob;
		}

		.offer__text{
			font-size: 13px;
		}

		.principles{
			font-size: 13px;
		}

		.fp-controlArrow{
			display: none;
		}
	}

	.section{
		padding-top: 40px;


		.section__item{
			padding-left: 10px;
			padding-right: 10px;
		}

		.section__image{
			margin-bottom: 20px;
		}

		.section__title{
			font-size: 18px;
			margin-bottom: 20px;
		}

		.section__content {
			flex-direction: column;
		}

		.section__part{
			width: 100%;
		}
	}

	.footer{
		.input-group{
			grid-template-columns: 1fr;
		}

		.checkbox{
			align-items: flex-start;
			display: block;
		}
	}

}