// Basics
@font-face{
	font-family: Helvetica Regular;
	font-weight: 400;
	src: url('../fonts/HelveticaNeueCyr-Roman.ttf') format("truetype");
}

@font-face{
	font-family: Helvetica Bold;
	font-weight: 400;
	src: url('../fonts/HelveticaNeueCyr-Bold.ttf') format("truetype");
}

*{
	box-sizing: border-box;
}

img{
	max-width: 100%;
}

$main-color: #2B2E38;
$accent-color: #79745B;

#cursor{
	content: "";
	--size: 20px;
	position: absolute;
	left: var(--x);
	top: var(--y);
	width: var(--size);
	height: var(--size);
	border-radius: 100px;
	border: 1px solid #fff;
	z-index: 99;
}

::selection{
	background: lighten($accent-color, 20%);
	color: #fff;
}

::-webkit-selection{
	background: lighten($accent-color, 20%);
	color: #fff;
}

::-ms-selection{
	background: lighten($accent-color, 20%);
	color: #fff;
}

::-o-selection{
	background: lighten($accent-color, 20%);
	color: #fff;
}

// Specification & Reset
body{
	margin: 0;
	padding: 0;
	font-family: Helvetica Regular;
	cursor: none;
}


button, a, li{
	cursor: none;
}

ul,li,p,h1,h2,h3{
	margin: 0;
	padding: 0;
}

a{
	color: #fff;
	text-decoration: none
}

.container{
	width: 1136px;
	margin: 0 auto;	
}

.d-flex{
	display: flex;
}

.d-grid{
	display: grid
}

.container-full{
	width: 1662.5px;
	margin: 0 auto;
}

.bg-line{
	width: 41px;
	height: 100%;
	position: absolute;
	z-index: 2;
}

.bg-line-left{
	background: url('../img/222.png') repeat-y left top / contain;
}

.bg-line-right{
	background: url('../img/222.png') repeat-y right top / contain;
	right: 0;
	transform: scale(-1, 1);
}

.animation{
	animation-duration: 1s;
	animation-fill-mode: forwards;
}

.line-animation{
	@extend .animation;

	animation-delay: 2.5s;
	opacity: 0;
}

.btn{
	background: $accent-color;
	border: 0;
	color: #fff;
	text-transform: uppercase;
	height: 52px;
	line-height: 52px;
	padding: 0 30px;
	border-radius: 5px;
	margin-top: 40px;
	transition: all .3s ease;
	&:hover{
		background: darken($accent-color, 10%);
		box-shadow: 0 0 20px rgba(121, 116, 91, 0.5)
	}
}

// Menu
.nav{
	opacity: 0;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 7;

	align-items: center;

	margin-top: 20px;
	width: 100%;
	padding-left: 100px;

	animation-name: menu-animation;
	animation-delay: 2.5s;
	@extend .animation;
}

#menu{
	display: flex;
	width: 100%;
	list-style: none;
	

	.button {
		position: relative;
		display: inline-block;
		padding: 10px 20px;
		overflow: hidden;
		transition: 0.2s;
		user-select: none;
		outline: none;
		margin: 0px;
	}

	.button:hover .button__line--top {
	  transform: translateX(100%);
	  transition: 1s 0.25s;
	}

	.button:hover .button__line--bottom {
	  transform: translateX(-100%);
	  transition: 1s 0.25s;
	}

	.button:hover .button__line--left {
	  	transform: translateY(-100%);
	    transition: 1s;
	}

	.button:hover .button__line--right {
	  	transform: translateY(100%);
	    transition: 1s;
	}

	.button__line {
	  position: absolute;
	  border-radius: 2px;
	  z-index: 999;
	  
		&--top {
	    	top: 0px;
	    	left: -1px;
	    	width: 100%;
	    	height: 2px;
	    	transform: translateX(-100%);
	        background-image: linear-gradient(to right, transparent, #fff);
	  	}
	  
	  	&--bottom {
	    	bottom: 0;
	    	left: 1px;
	    	width: 100%;
	    	height: 2px;
	    	transform: translateX(100%);
	        background-image: linear-gradient(to left, transparent, #fff);
	  	}
	  
	  	&--left {
	    	bottom: 0;
	    	left: 0;
	    	width: 2px;
	    	height: 100%;
	    	transform: translateY(100%);
	        background-image: linear-gradient(to top, transparent, #fff);
	  	}
	  
	  	&--right {
	    	bottom: 0;
	    	right: 0;
	    	width: 2px;
	    	height: 100%;
	    	transform: translateY(-100%);
	        background-image: linear-gradient(to bottom, transparent, #fff);
	  	}
	}
}

.hamburher{
	width: 30px;
	height: 16px;
	position: relative;
	display: none;
	// margin-right: 10px;
	z-index: 7;

	&.active{
		span:first-child,
		span:last-child{
			top: 7px;
			opacity: 0;
		}

		span:nth-child(2){
			transform: rotate(45deg);
		}

		span:nth-child(3){
			transform: rotate(-45deg);
		}
	}

	span{
		display: block;
		width: 100%;
		height: 2px;
		background: #fff;
		border-radius: 5px;
		position: absolute;
		left: 0;
		transition: all .3s ease;

		&:first-child{
			top: 0;
		}

		&:nth-child(2),
		&:nth-child(3){
			top: 7px;
		}

		&:last-child{
			top: 14px;
		}
	}
}

.mobile-nav{
	width: 50%;
	height: 100vh;
	background: rgba(43, 46, 56, 0.97);
	position: fixed;
	top: 0;
	right: -100%;
	z-index: 4;
	padding: 100px 70px 0 0;
	text-align: right;
	transition: all 1s ease-in-out;

	&.is-open{
		right: 0;
	}

	&__list{
		list-style: none;
	}

	&__link{
		font-size: 22px;
		margin: 20px 0;
		display: block;

	}
}

// Hero
header{
	background: url('../img/bg1.png') no-repeat center top / cover;
	height: 100vh;

	.wrapper{
		grid-template-columns: 1fr 1fr;
		align-items: center;
		height: 100%;
	}

	.blur{
		position: absolute;
		left: 0;

		height: 100%;
		width: 50%;

		transform: translateX(-100%);
        -webkit-backdrop-filter: blur(5px);
        backdrop-filter: blur(5px);

        animation-name: blur-animation;
        animation-delay: 1.7s;
       	@extend .animation;
	}

	.left-part, .right-part{
		height: 100%;
	}

	// Logo
	.logo,
	.logo .logo__top,
	.logo .logo__bottom {
		position: absolute;
		@extend .animation;
	}

	.logo{
		width: 50%;
		height: 200px;

		display: flex;
		justify-content: center;

		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		animation-name: logo-box-animation;
		animation-delay: 1.7s;
	}

	.logo__top{
		width: 218px;
		animation-name: logo-top-animation, logo-top-size-animation;
		animation-delay: 0.7s, 1.7s;

		opacity: 0;
	}

	.logo__bottom{
		width: 1044px;
		
		animation-name: logo-bottom-animation, logo-bottom-size-animation;
		animation-delay: 0.5s, 1.7s;
	}

	.right-part{
		padding-right: 40px;
		position: relative;
		color: #fff;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	// Offer
	.offer{
		position: relative;
		left: 200%;

		animation-name: offer-animation;
		animation-delay: 1.7s;
		@extend .animation;
	}

	.offer__text{
		line-height: 24px;
		text-align: right;
	}

	.offer__btn{
		@extend .btn;
	}

	// Slider
	.principles{
		position: relative;
		opacity: 0;
		bottom: -100px;
		margin-bottom: 100px;
		
		animation-name: slider-animation;
		animation-delay: 1.7s;
		@extend .animation;
	}

	.principles-slider__item{
		text-align: center;
	}

	.fp-tableCell{
		height: auto !important;
	}

	.fp-controlArrow{
		border: none;
		width: 18px !important;
		top: unset;
		bottom: -8px;
		height: 34px;
		background: url('../img/arrow-left.svg') no-repeat center center / contain;
	}

	.fp-controlArrow.fp-prev{
		left: 50px;
	}

	.fp-controlArrow.fp-next{
		background: url('../img/arrow-right.svg') no-repeat center center / contain;
		right: 50px;
	}

	.fp-slidesNav{
		display: none;
	}
}

// Section Common styles
.section{
	background-color: $main_color;
	color: $accent-color;
	line-height: 1.5em;

	.section__title{
		text-transform: uppercase;
		font-size: 24px;
		margin-bottom: 40px;
		text-align: center;
		color: #fff;
	}

	.section__title_theme_dark{
		color: $accent-color;
	}

	.section__content{
		flex-wrap: wrap;
		justify-content: space-between;
	}

	.section__part{
		width: 48%;
	}

	.section__image img{
		box-shadow: 0 0 50px rgba(0,0,0,0.8);
	}

	.section__item{
		padding: 23px;
		margin-bottom: 0;
		background: #464951;
		color: #fff;
		box-shadow: 0 0 50px rgba(0,0,0,0.8);
		transition: all .3s ease;

		&:hover{
			box-shadow: none
		}
	}

	.section__item_theme_light{
		background: $accent-color;
		margin-top: 31px;
	}

	.item__title{
		text-transform: uppercase;
		font-size: 18px;
		margin-bottom: 13px;
	}

	.item__text{
		overflow-y: auto;
	}

	.item__list{
		margin-left: 20px;
	}
}

// Crew
.crew{
	background: $main-color url('../img/bg2.png') no-repeat center center / cover;
}

// Salary
.salary{
	padding: 50px 0;
}

// Footer
.footer{
	padding-top: 7vh;
	.order-form{
		width: auto;
		max-width: 581px;
		margin: 0 auto;
		text-align: center;
	}
	input, textarea{
		outline: none;
		border-radius: 5px;
		border: 0;
		padding: 16px 13px;
	}

	.input-group{
		grid-template-columns: 1fr 1fr;
		grid-gap: 17px;
	}

	.order-form__message{
		width: 100%;
		height: 120px;
		margin-top: 17px;
	}

	.checkbox{
		display: block;
		text-align: center;
		margin: 17px 0;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		cursor: pointer;
	}

	.checkbox__input{
		display: none;
	}

	.checkbox__input:checked + .checkbox__icon .tick{
		display: block;
	}

	.checkbox__icon{
		width: 19px;
		height: 19px;
		background: #fff;
		display: inline-block;
		content: "";
		margin-right: 11px;

		.tick{
			display: none;
		}
	}

	.checkbox__body{
		color: #fff;
	}

	.checkbox__body a {
		text-decoration: underline;
	}

	.order-form__button{
		@extend .btn;
	}

	.logo-line{
		padding: 0 40px;
		margin-top: 10vh;
	}

	.logo-line img{
		margin: 0 15px;
	}

	.copyright{
		text-align: center;
		margin: 40px 0 35px 0;
	}
}

@import "animation";
@import "responsive"