// Desktop animation
@keyframes logo-top-animation{
	from{
		margin-left: -30%;
		transform: translateX(30%) translateY(0);

		opacity: 0;
	}	

	to{
		margin-left: 0;
		transform: translateX(0) translateY(-90px);

		opacity: 1;
	}
}

@keyframes logo-bottom-animation{
	from{
		transform: translate(0, 0);
	}

	to{
		transform: translate(0, 100px);
	}
}

@keyframes logo-box-animation{
	from{
		left: 50%;
		transform: translate(-50%, -50%);
	}

	to{
		left: 0;
		transform: translate(0, -50%);
	}
}

@keyframes logo-top-size-animation{
	from{
		width: 218px;
		transform: translateX(0) translateY(-90px);
	}

	to{
		width: 90px;
		transform: translateX(0) translateY(0px);
	}
}

@keyframes logo-bottom-size-animation{
	from{
		width: 1044px
	}

	to{
		width: 370px
	}
}

@keyframes blur-animation{
	from{
		transform: translateX(-100%);
	}

	to{
		transform: translateX(0);
	}
}

@keyframes menu-animation{
	from{
		opacity: 0;
	}

	to{
		opacity: 1;
	}
}

@keyframes offer-animation{
	from{
		left: 200%;
	}

	to{
		left: -50px;
		top: 100px;
	}
}

@keyframes slider-animation{
	from{
		opacity: 0;
		bottom: -100px;
	}

	to{
		opacity: 1;
		bottom: 0;
	}
}

// Tablet animation
 @keyframes logo-box-animation--tablet{
	from{
		left: 50%;
		transform: translate(-50%, -50%);
	}

	to{
		top: 100px;
		transform: translate(-50%, 0);
	}
}

@keyframes blur-animation--tablet{
	from{
		transform: translateX(0) translateY(-100%);
	}

	to{
		transform: translateX(0) translateY(0);
	}
}

@keyframes offer-animation--tablet{
	from{
		left: 200%;
	}

	to{
		left: -50px;
		top: 50px;
	}
}

// Mobile animation
@keyframes logo-box-animation--mob{
	from{
		transform: translate(-50%, -50%);
	}

	to{
		top: 20px;
		transform: translate(-50%, 0);
	}
}

@keyframes logo-top-animation--mob{
	from{
		margin-left: -30%;
		transform: translateX(30%) translateY(0);

		opacity: 0;
	}	

	to{
		margin-left: 0;
		transform: translateX(0) translateY(0px);

		opacity: 1;
	}
}

@keyframes logo-top-size-animation--mob{
	from{
		width: 100px;
		transform: translateX(0) translateY(0px);
	}

	to{
		width: 90px;
		transform: translateX(0) translateY(0px);
	}
}

@keyframes offer-animation--mob{
	from{
		left: 200%;
	}

	to{
		left: 0px;
		top: 30vh;
	}
}